body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(51, 49, 49);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.book-list {
  left: 100px;
}

.before {
  text-decoration: line-through;
}
.sale {
  background-color: red;
  width: fit-content;
  color: aliceblue;
  margin-top: 100px;
}
.myimage {
  background-repeat: no-repeat;
  align-content: center;
  width: 500px;
  height: 300px;
}
.bookname {
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-weight: 900;
  color: aliceblue;
}
.bookwriter {
  color: aliceblue;
}
.search {
  width: 99%;
  height: 50px;
  color: rgb(240, 68, 68);
  font-weight: bolder;
  background-color: rgb(27, 27, 27);
}
.book-list {
  padding-top: 10px;
  padding-left: 20px;
}
.ADDTOCART {
  background-color: rgb(243, 67, 67);
  width: fit-content;
}

.qty {
  width: fit-content;
}
.checkout {
  background-color: rgb(248, 160, 248);
  width: fit-content;
}
.remove {
  background-color: rgb(240, 68, 68);
}

.MOREDETILS {
  width: fit-content;
  font-weight: bold;
}
.showing {
  color: rgb(240, 68, 68);
  font-weight: bolder;
}

.showingbtn {
  font-size: 10px;
  padding-left: 10px;
  color: rgb(7, 7, 7);
  margin-left: 10px;
}
